import React, { useCallback, useEffect, useState } from "react";
import styled from "styled-components";
import palette from "../../lib/palette";

const TagBoxBlock = styled.div`
    width:100%;
    border-top:1px solid ${palette.gray[2]};
    padding-top:2rem;

    h4{
        color :${palette.gray[8]};
        margin:0 0 0.5rem 0;
    }
`;

const TagForm = styled.form`
    border-radius:4px;
    overflow : hidden;
    display:flex;
    width:256px;
    border:1px solid ${palette.gray[9]};

    input,button{
        outline : none;
        border:none;
        font-size:1rem;
    }

    input{
        padding:0.5rem;
        flex:1;
    }

    button{
        cursor:pointer;
        padding:0 1rem;
        border:none;
        background:${palette.gray[8]};
        color:white;
        font-weight:bold;
        &:hover{
            background:${palette.gray[6]};
        }
    }
`;

const Tag = styled.div`
    margin-right : 0.5rem;
    color:${palette.gray[6]};
    cursor:pointer;
    &:hover{
        opacity:0.5;
    }   
`;

const TagListBlock = styled.div`
    display:flex;
    margin-top:0.5rem;
`;

// react.memo 사용 시 받아오는 실제 props 변경시에만 리렌더링
const TagItem = React.memo(({tag , onRemove}) => (<Tag onClick={() => onRemove(tag)}>#{tag}</Tag>));

const TagList = React.memo(({tags, onRemove}) => (
    <TagListBlock>
        {tags.map(tag => (
            <TagItem key={tag} tag={tag} onRemove={onRemove}/>
        ))}
    </TagListBlock>
));

export const TagBox = ({tags, onChangeTags}) => {
    const [input, setInput] = useState('');
    const [localTags, setLocalTags] = useState([]);

    const insertTag = useCallback(
        tag => {
            if(!tag) return; //공백추가 X
            if(localTags.includes(tag)) return; //이미있는태그추가 X

            const nextTags = [...localTags,tag];
            setLocalTags(nextTags);
            onChangeTags(nextTags);
        },
        [localTags,onChangeTags],
    );

    const onRemove = useCallback(
        tag => {
            const nextTags2 = localTags.filter(t => t !== tag);
            setLocalTags(nextTags2);
            onChangeTags(nextTags2);
        },
        [localTags,onChangeTags],
    );

    const onChange = useCallback(e => {
        setInput(e.target.value);
    },[])

    const onSubmit = useCallback(
        e => {
            e.preventDefault();
            insertTag(input.trim());
            setInput('');
        },
        [input,insertTag],
    );

    useEffect(()=>{
        setLocalTags(tags);
    },[tags]);


    return(
        <TagBoxBlock>
            <h4>태그</h4>
            <TagForm onSubmit={onSubmit}>
                <input placeholder="태그를 입력해주세요" value={input} onChange={onChange}/>
                <button type="submit">추가</button>
            </TagForm>
            <TagList tags={localTags=== undefined ? 10 : localTags} onRemove={onRemove}/>
        </TagBoxBlock>

    );
};